import React from "react"
import Layout from "../components/layout"
import { graphql } from "gatsby"
import NewsletterFormS from "../components/newsletterForm"
import NewsletterBox from "../components/newsletterBox"
import SEO from "../components/seo"
import Container from 'react-bulma-components/lib/components/container'

export default ({ data }) => {
  return (
    <Layout>
      <SEO title="Archiv" keywords={[`workshops`, `handmade`, `kurse`]} />
      <Container Fluid>
        <NewsletterFormS />
        {data.allJsonJson.edges.map(({ node }, index) => (
          <NewsletterBox node={node} />
        ))}
      </Container>
    </Layout>
  )
}

export const query = graphql`
  query($slug: String!) {
    allJsonJson(filter: { slug: { eq: $slug } }) {
      edges {
        node {
          published
          title
          tags
          newsletter
        }
      }
    }
  }
`
