import React from "react"
import Container from 'react-bulma-components/lib/components/container';
import Hero from 'react-bulma-components/lib/components/hero';
import Level from 'react-bulma-components/lib/components/level';
import Icon from 'react-bulma-components/lib/components/icon';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import faAsterisk from "@fortawesome/fontawesome-free-solid/faAsterisk"
import faArchive from "@fortawesome/fontawesome-free-solid/faArchive"
import faLock from "@fortawesome/fontawesome-free-solid/faLock"
import faCopyright from "@fortawesome/fontawesome-free-solid/faCopyright"
import { Link } from "gatsby"

const Layout = ({ children, pageInfo }) => {
  return (
    <Container fluid>
      <Hero size="fullheight">
        <Hero.Head renderAs="header">
          <Level>
            <Level.Side align="left">
              <Link to="/aktuell">
                <Icon align="left">
                  <FontAwesomeIcon align="left" icon={faAsterisk} />
                </Icon>
                Aktuell
              </Link>
            </Level.Side>
            <Level.Side align="right">
              <Link to="/archiv">
                Archiv
                <Icon align="left">
                  <FontAwesomeIcon align="left" icon={faArchive} />
                </Icon>
              </Link>
            </Level.Side>
          </Level>
        </Hero.Head>
        <Hero.Body>{children}</Hero.Body>
        <Hero.Footer>
          <Level>
            <Level.Side align="left">
              <Link to="/datenschutz">
                <Icon align="left">
                  <FontAwesomeIcon align="left" icon={faLock} />
                </Icon>
                Datenschutz
              </Link>
            </Level.Side>
            <Level.Side align="right">
              <Link to="/impressum">
                Impressum
                <Icon align="left">
                  <FontAwesomeIcon align="left" icon={faCopyright} />
                </Icon>
              </Link>
            </Level.Side>
          </Level>
        </Hero.Footer>
      </Hero>
    </Container>
  )
}

export default Layout
